.container > .row#caseconverter {
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: auto auto auto;
  grid-template-areas:
    "topleft topright"
    "middleleft middleright"
    "bottom .";
  width: 100%;
  row-gap: 10px;
  margin: auto;
  align-items: center;
}
.row#caseconverter > .item1 {
  grid-area: topleft;
  width: auto;
}
.row#caseconverter > .item2 {
  grid-area: topright;
  width: auto;
}
.row#caseconverter > .item3 {
  grid-area: middleleft;
  width: auto;
}
.row#caseconverter > .item4 {
  grid-area: middleright;
  width: auto;
}
.row#caseconverter > .item5 {
  grid-area: bottom;
  width: auto;
  justify-self: center;
}

@media (max-width: 1000px) /*900px before*/ {
  .row {
    width: auto;
    float: none;
  }
}
@media screen and (max-width: 52em) {
  .project-nav,
  .row,
  .col-lg-6 {
    bottom: -1rem; /*-90rem before*/
  }
}
